  }

  .radar-image {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    backface-visibility: hidden;
  }

  .radar-middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%)
  }

  .radar-container:hover .radar-image {
    opacity: 0.3;
  }

  .radar-container:hover .radar-middle {
    opacity: 1;
  }

  .radar-text {
    background-color: #4bafac;
    color: white;
    font-size: 16px;
    padding: 16px 32px;
  }

  .radar-image-slide-bottom {
    display: block;
    width: 100%;
    height: auto;
  }

  .radar-overlay-slide-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(98, 175, 208, 0.56);
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: .5s ease;
  }

  .radar-container-slide-bottom:hover .radar-overlay-slide-bottom {
    height: 100%;
  }

  .radar-text-slide-bottom {
    white-space: nowrap;
    color: white;
    font-size: 20px;
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
